import  React from "react"
import { useState ,useEffect} from 'react';
import {Input, message, Modal, Spin,Statistic} from 'antd';
import './sell.css';
import v2Abi from "./abi/ERC20.json";
import BigNumber from 'bignumber.js'
import FomoProxy from "./abi/FomoProxy.json";
import FomoToken from "./abi/FomoToken.json";
import Lptoken from "./abi/LP.json";
import Uniswap from "./abi/UniswapV2Router.json";
import UniswapPair from "./abi/UniswapV2Pair.json";
import LPToken from "./abi/LP.json";
import {ethers} from "ethers";
import { BorderTopOutlined,LoadingOutlined} from '@ant-design/icons';
import { useTranslation } from 'react-i18next';
const { confirm } = Modal;


function App() {
  const { t,i18n} = useTranslation();
  const [isloading,checkloading] = useState(1);
  const changeloading = (item) => {
    checkloading(isloading => item)
  };
  useEffect(()=>{
    window.ethereum.enable().then(()=>{
      loaddata();
    })
  },[])
  //myfomo
  const [myfomo,checkmyfomo] = useState(0);
  const changemyfomo = (item) => {
    let Bignredeem = new BigNumber(item.toString());
    let mycoinredeem = Bignredeem.div(10**18).toString();
    mycoinredeem = Number(mycoinredeem)

    checkmyfomo(myfomo => item)
    if(mycoinredeem == 0){
      changestat("desable")
    }

  };
  const NumFixed = (item) => {
    let Bignredeem = new BigNumber(item.toString());
    let mycoinredeem = Bignredeem.div(10**18).toString();
    let slp = mycoinredeem.split(".");
    if(slp.length > 1){
      let pock = slp[0]+'.'+slp[1].substr(0,2)
      return pock;
    }else{
      return  slp[0]
    }
  }
  //totalFOMO
  const [totalfomo,checktotalfomo] = useState(0);
  const changetotalfomo = (item) => {
    item = Number(item).toFixed(2)
    checktotalfomo(totalfomo => item)
  };
  //totalUsdt
  const [totalUsdt,checktotalUsdt] = useState(0);
  const changetotalUsdt = (item) => {
    item = Number(item).toFixed(2)
    checktotalUsdt(totalUsdt => item)
  };
  const loaddata = async() => {
    if (window.dapp) {
      changeloading(1)
      let allowance = await window.dapp.getAllowance(window.dapp.currentAccount.address, window.FomoToken, window.SwapRouter, FomoToken, 18);
      // let allowance = await window.dapp.getAllowance(window.dapp.currentAccount.address, window.usdtAddress, window.FomoProxy, v2Abi, 18);
      // const allowance = await window.dapp.approve(window.FomoToken, FomoToken, window.SwapRouter, window.uintmax);

      allowance = parseInt(allowance)
      if (allowance > 0) {
        checkstat("approve")
      }else{
        checkstat("wait")
      }
      //mytudst
      const wonrUsdt = await window.dapp.queryContract(window.LP, Lptoken, "balanceOf",window.dapp.currentAccount.address)
      var Bign = new BigNumber(wonrUsdt.toString());
      var mycoin = Bign.div(10**18).toString();

      //myfomo
      const fomocion = await window.dapp.queryContract(window.FomoToken, FomoToken, "balanceOf",window.dapp.currentAccount.address)
      var fomoBign = new BigNumber(fomocion.toString());
      var fomomycoin = fomoBign.div(10**18).toString();
      changemyfomo(fomocion)

      //FOMO
      const sharefomocion = await window.dapp.queryContract(window.FomoToken, FomoToken, "balanceOf",window.LP)
      var sharefomoBign = new BigNumber(sharefomocion.toString());
      var sharefomomycoin = sharefomoBign.div(10**18).toString();
      changetotalfomo(sharefomomycoin)
      //shareUsdt
      const myredeem2 = await window.dapp.queryContract(window.usdtAddress,v2Abi,"balanceOf",window.LP)
      let Bignredeem2 = new BigNumber(myredeem2.toString());
      let mycoinredeem2 = Bignredeem2.div(10**18).toString();
      changetotalUsdt(mycoinredeem2)

      //LP
      const myredeem1 = await window.dapp.queryContract(window.LP,LPToken,"totalSupply",[])
      let Bignredeem1 = new BigNumber(myredeem1.toString());
      let mycoinredeem1 =Bignredeem1.div(10**18).toString();
      changetotalLP(mycoinredeem1)

      changeloading(0)
      getbuyLiquidityLogs(0);
    }else{
      let self = this;
      setTimeout(function (){
        loaddata();
      },1000)
    }
  }
  //payprice
  const [payprice,checkprice] = useState(0);
  const changePrice = (item) => {

    item = Number(item);
    checkprice(item)

    changeestimate()
  };
  const maxPrice = () => {
    let item = myfomo.toString()
    if(myfomo == 0){
      return false;
    }
    let Bignredeem = new BigNumber(item);
    let mycoinredeem = Bignredeem.div(10**18).toString();
    let slp = mycoinredeem.split(".");
    let pock;

    if(slp.length > 1){
       pock = slp[0]+'.'+slp[1].substr(0,2)
    }else{
      pock = slp[0]
    }

    changePrice(pock)
    changeestimategg(pock)
    // changeestimate()
    // let items = totalUsdt/totalfomo*Number(pock);
    // console.log(items)
    // changeestimate(items)
  };
  //预计
  const [estimate,checkestimate] = useState(0);
  const changeestimate = () => {

      let item = totalUsdt/totalfomo*payprice;

      item = item.toFixed(2)
      checkestimate(item)


  };
  const changeestimategg = (item) => {

    let items = totalUsdt/totalfomo*item;

    items = items.toFixed(2)
    checkestimate(items)
  };
  //stat
  const [stat,checkstat] = useState("wait");
  const changestat = (item) => {
    checkstat(stat => item)
  };
  const [buyhistory,checkbuyhistory] = useState([]);
  const changebuyhistory = (item) => {

    checkbuyhistory(buyhistory => item)
  };

  const loadmore = () => {
    let cstsds = t('t42')

    changemore(cstsds)

    getbuyLiquidityLogs(cobopl)
  }
  const dateFormattime =  (time) => {
    time = time + "000"
    time = parseInt(time)

    const t = new Date(time)
    // 日期格式
    const format = 'h:i'
    // const format = 'm-d'
    const year = t.getFullYear()


    // 由于 getMonth 返回值会比正常月份小 1
    const month = t.getMonth() + 1
    const day = t.getDate()
    const hours = t.getHours()
    const minutes = t.getMinutes()
    const seconds = t.getSeconds()
    const hash = {
      'Y': year,
      'm': month,
      'd': day,
      'h': hours,
      'i': minutes,
      's': seconds
    }
    return format.replace(/\w/g, o => {
      return hash[o]
    })
  }
  const [cobopl,checkstartBlock] = useState(0);
  const changestartBlock = (item) => {
    checkstartBlock(cobopl => item)
  };
  const getbuyLiquidityLogs = async (item) => {
    let cstsd = t('t42')
    changeload(cstsd)

    let lastBlocks = 0;
    const perBlock = 5000;//每次查询多少个区块，最大5000
    let startBlock; // 开始块数
    let endBlock;
    if(item != 0){
      console.log(item)
      lastBlocks = item -perBlock;
      endBlock = lastBlocks;
      startBlock =lastBlocks-perBlock; // 开始块数
      changestartBlock(lastBlocks);
    }else{
      lastBlocks = await window.dapp.rpc.getBlockNumber();
      changestartBlock(lastBlocks);
      console.log(item)
      startBlock = lastBlocks - perBlock; // 开始块数
      endBlock = lastBlocks;
    }
    console.log(lastBlocks)
    const UniswapPairAddress = window.LP;
    const UniswapPairContract = new ethers.Contract(UniswapPairAddress,UniswapPair, window.dapp.rpc);

    const events = await window.dapp.rpc.send('eth_getLogs', [{
      address: [
        UniswapPairContract.address
      ],
      fromBlock: '0x'+(startBlock).toString(16),
      toBlock: '0x'+(endBlock).toString(16),
      topics: [
        [ // topic[0]
          UniswapPairContract.filters.Swap().topics[0]
        ]
      ]
    }]);
    console.log(events)
    let b = []
    if(events.length == 0){
      setTimeout(function (){
        getbuyLiquidityLogs(lastBlocks);
      },1000)
      return  false;
    }else{
      for(var i in events){
        let hxst = await window.dapp.rpc.getBlockWithTransactions(events[i].blockHash)
        b.push(hxst)
      }
    }
    console.log("b",b)
    let listArr= events.map((item,index)=>{ // for is serial, forEach is parallel (i.e. asynchronous)
      const event = events[index];
        const decodedEvent = UniswapPairContract.interface.decodeEventLog(
          "Swap", // Maybe there is a more semantic way to specify this
          event.data,
          event.topics
        );
        console.log(decodedEvent);
        let sender=decodedEvent.sender;
        let to=decodedEvent.to;
        //数
        let usdtAmount0=ethers.utils.formatUnits(decodedEvent.amount0In,18).toString();
        let usdtAmount1=ethers.utils.formatUnits(decodedEvent.amount1In,18).toString();
        let fomoAmount0=ethers.utils.formatUnits(decodedEvent.amount0Out,18).toString();
        let fomoAmount1=ethers.utils.formatUnits(decodedEvent.amount1Out,18).toString();

        usdtAmount0 = Number(usdtAmount0).toFixed(2);
        usdtAmount1 = Number(usdtAmount1).toFixed(2);
        fomoAmount0 = Number(fomoAmount0).toFixed(2);
        fomoAmount1 = Number(fomoAmount1).toFixed(2);
        if(to.toUpperCase() == (window.FomoProxy).toUpperCase()){
          if(usdtAmount0 == 0.00){
            //买币
            return(
              <div className="sellhistorylistli">
                <div className="ril">
                  <div className="cor">
                    <div>{dateFormattime(b[index].timestamp)}</div>
                    <div>{(usdtAmount1/fomoAmount0).toFixed(7)}</div>
                    <div><span className="green">{fomoAmount0}</span></div>
                    <div>{usdtAmount1}</div>
                    <div className="blue"><a href={"https://bscscan.com/address/"+to}>{formDete12(to)}</a></div>
                  </div>
                </div>
              </div>
            )
          }else{
            //买币
            return(
              <div className="sellhistorylistli">
                <div className="ril">
                  <div className="cor">
                    <div>{dateFormattime(b[index].timestamp)}</div>
                    <div>{(usdtAmount0/fomoAmount1).toFixed(7)}</div>
                    <div><span className="green">{fomoAmount1}</span></div>
                    <div>{usdtAmount0}</div>
                    <div className="blue"><a href={"https://bscscan.com/address/"+to}>{formDete12(to)}</a></div>
                  </div>
                </div>
              </div>
            )
          }
        }else{
          if(fomoAmount0 == 0.00){
            return(
              <div className="sellhistorylistli">
                <div className="ril">
                  <div className="cor">
                    <div>{dateFormattime(b[index].timestamp)}</div>
                      <div>{(fomoAmount1/usdtAmount0).toFixed(7)}</div>
                    {/*<div>{(usdtAmount/fomoAmount).toFixed(7)?(usdtAmount/fomoAmount).toFixed(7):0 }</div>*/}
                    <div><span className="red">{usdtAmount0}</span></div>
                    <div>{fomoAmount1}</div>
                    <div className="blue"><a href={"https://bscscan.com/address/"+to}>{formDete12(to)}</a></div>
                  </div>
                </div>
              </div>
            )
          }else{
            return(
              <div className="sellhistorylistli">
                <div className="ril">
                  <div className="cor">
                    <div>{dateFormattime(b[index].timestamp)}</div>
                    <div>{(usdtAmount1/fomoAmount0).toFixed(7)}</div>
                    {/*<div>{(usdtAmount/fomoAmount).toFixed(7)?(usdtAmount/fomoAmount).toFixed(7):0 }</div>*/}
                    <div><span className="red">{usdtAmount1}</span></div>
                    <div>{fomoAmount0}</div>
                    <div className="blue"><a href={"https://bscscan.com/address/"+to}>{formDete12(to)}</a></div>
                  </div>
                </div>
              </div>
            )
          }

        }
    })

    listArr.reverse();
    let its = buyhistory;
    if(buyhistory.length > 0){

      changebuyhistory(its.concat(listArr))
    }else{
      changebuyhistory(listArr)
    }
    let csts = t('t41')
    changeload(csts)
    changemore(csts)
  }
  const formDete12 = (str) => {
    return str.substr(0,2)+"****"+str.substr(-4);
  };
  const approvesell = async () => {
    changeloading(1)
    if (window.dapp) {
      try {
        const valueofapprove = await window.dapp.approve(window.FomoToken, FomoToken, window.SwapRouter, window.uintmax);
        let hash = '' + valueofapprove.hash;
        await window.dapp.rpc.waitForTransaction(hash, 1)
        checkstat("approve")
        changeloading(0)
      } catch (e) {
        changeloading(0)
        message.error(e.message);
        checkstat("wait")
      }
    }
  }
  const formDete10 = (str) => {
    return str.substr(0,2)+"****"+str.substr(-6);
  };
  const sell = async () => {
    if (window.dapp) {
      if(payprice == "0" || payprice == " " || payprice == ""){
        message.error("Please enter a value");
        changeloading(0)
        return false;
      }
      if(isNaN(payprice)){
        message.error('please enter a number');
        return false;
      }
      changeloading(1)
      let priceWei = window.dapp.parseEther(payprice.toString())

      let ts=Number((new Date().getTime() + 60000)/1000).toFixed(0);
      try {
        const txs = await window.dapp.executeContract(window.SwapRouter,Uniswap,"swapExactTokensForTokens", [priceWei,0,[window.FomoToken,window.usdtAddress],window.dapp.currentAccount.address,ts])
        let txhash = ''+txs.hash;
        await window.dapp.rpc.waitForTransaction(txhash,1)
        checkhaxcaeh(txhash)
        loaddata();
        changePrice(0)
        checkestimate(0)
        changeloading(0)
        checkstat("wait")
      }catch (e) {
        changeloading(0)
        message.error(e.message);
        checkstat("approve")
      }

    }
  }
  const inputchange = (e) => {
    let it = parseInt(e.target.value);
      if(it > myfomo){
        changePrice(myfomo)
        changeestimate()
      }else{
        changePrice(e.target.value)
        changeestimate()
      }


  }

  const [loading,checkload] = useState("加载中");
  const changeload = (item) => {
    checkload(loading => item)
  };
  let cstssd = t('t41')
  let [more,checkmore] = useState(cstssd);
  const changemore = (item) => {
    checkmore(more => item)
  };
  //totalLP
  const [totalLP,checktotalLP] = useState(0);
  const changetotalLP = (item) => {
    item = Number(item).toFixed(2)
    checktotalLP(totalLP => item)
  };
  const [haxcaeh,checkhaxcaeh] = useState("");
  const router = () => {
    window.location = "https://bscscan.com/tx/"+haxcaeh;
  }
  const closepop = () => {
    checkhaxcaeh("")
  }
  const antIcon = <LoadingOutlined style={{ fontSize: 24 }} spin />;
  return (
    <div className="sell">
      <div className="index">
        {haxcaeh!=""?
          <div className="logs animated fadeIn">
            <span></span>
            <div className="tit">{t('t51')}</div>
            <div className="tis" onClick={router.bind(this)}>On BscScan：{formDete10(haxcaeh)}</div>
            <div className="close" onClick={closepop.bind(this)}>X</div>
          </div>:""
        }
        <div className="indexpart">
          <div className="parlinek k2"><div>FOMO/USDT</div><div><span>$<Statistic  value={(totalUsdt*2).toFixed(2)} /></span></div></div>
          <div className="parlinek k3"><div>{t('t29')}</div><div><span><Statistic  value={totalfomo} /></span></div></div>
          <div className="parlinek k4"><div>{t('t30')}</div><div><span><Statistic  value={totalUsdt} /></span></div></div>
          {/*<div className="parlinek"><div>LP价格</div><div><span>$ 1546.00</span></div></div>*/}
          <div className="sellbox">

            <div className="line2">
              <Input type="number" onChange={inputchange.bind(this)} value={payprice} />
              <div className="max" onClick={maxPrice.bind(this)}>MAX</div>
            </div>
            <div className="line1"><span>FOMO</span> <span><Statistic  value={NumFixed(myfomo)}/></span></div>
            <div className="line1"><span>{t('t34')}</span> <span><Statistic  value={estimate}/></span></div>
          </div>
          {stat=="approve"?
          <div className="btns" onClick={sell}>{t('t37')}</div>:""
          }
          {stat=="wait"?
          <div className="btns btnsauthor" onClick={approvesell}>{t('t35')}</div>:""
          }
          {stat=="desable"?
            <div className="btns desable">{t('t37')}</div>:""
          }
        </div>
        <div className="indexpart">
          <div className="sellhistorylist">
            <div>{t('t20')}</div>
            <div>{t('t38')}</div>
            <div>{t('t39')}</div>
            <div>{t('t40')}</div>
            <div>{t('t18')}</div>
          </div>
          {buyhistory!=""?
            buyhistory:<Spin indicator={antIcon} />
          }
          <div className="more" onClick={loadmore}>{more}</div>
        </div>
      </div>
      {
        isloading?<div className="loading">
          <Spin tip="Loading..." size="large"></Spin>
        </div>:""
      }
    </div>
  );
}

export default App;
